export const truncateString = (string, maxLength = 50) => {
    if (!string) return null;
    if (string.length <= maxLength) return string;
    return string ? `${string.substring(0, maxLength)}...` : '';
};

export const slugify = (word) => {
    let answer = "", a = {}, i;

    a[""]="YO";a[""]="I";a[""]="TS";a[""]="U";a[""]="K";a[""]="E";a[""]="N";a[""]="G";a[""]="SH";a[""]="SCH";a[""]="Z";a[""]="H";a[""]="'";
    a[""]="yo";a[""]="i";a[""]="ts";a[""]="u";a[""]="k";a[""]="e";a[""]="n";a[""]="g";a[""]="sh";a[""]="sch";a[""]="z";a[""]="h";a[""]="'";
    a[""]="F";a[""]="I";a[""]="V";a[""]="a";a[""]="P";a[""]="R";a[""]="O";a[""]="L";a[""]="D";a[""]="ZH";a[""]="E";
    a[""]="f";a[""]="i";a[""]="v";a[""]="a";a[""]="p";a[""]="r";a[""]="o";a[""]="l";a[""]="d";a[""]="zh";a[""]="e";
    a[""]="Ya";a[""]="CH";a[""]="S";a[""]="M";a[""]="I";a[""]="T";a[""]="'";a[""]="B";a[""]="YU";
    a[""]="ya";a[""]="ch";a[""]="s";a[""]="m";a[""]="i";a[""]="t";a[""]="'";a[""]="b";a[""]="yu";

    for (i in word){
        if (word.hasOwnProperty(i)) {
            if (a[word[i]] === undefined){
                answer += word[i];
            } else {
                answer += a[word[i]];
            }
        }
    }
    return answer.toLowerCase().replace(/[^\w ]+/g,'').replace(/ +/g,'_');
}